:root {
    --input-padding-x: 1.5rem;
    --input-padding-y: .75rem;
  }


.register.container{
    padding: 1rem;
    margin-top: 7%;
}


.card-signin .card-body {
    padding: 2rem;
  }
  

  .card-signin {
    border: 0;
    border-radius: 1rem;
    box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);
    overflow: hidden;
  }
  
  #register-form.form-signin {
    width: 100%;
  }
  
  #register-form.form-signin .btn {
    font-size: 80%;
    border-radius: 5rem;
    letter-spacing: .1rem;
    font-weight: bold;
    padding: 1rem;
    transition: all 0.2s;
  }
  
 #register-form .form-label-group {
    position: relative;
    margin-bottom: 1rem;
  }
  
 #register-form .form-label-group input {
    height: auto;
    border-radius: 2rem;
  }
  
 #register-form .form-label-group>input,
  .form-label-group>label {
    padding: var(--input-padding-y) var(--input-padding-x);
  }
  
 #register-form .form-label-group>label {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    margin-bottom: 0;
    line-height: 1.5;
    color: #495057;
    border: 1px solid transparent;
    border-radius: .25rem;
    transition: all .1s ease-in-out;
  }
  
 #register-form .form-label-group input::-webkit-input-placeholder {
    color: transparent;
  }
  
 #register-form .form-label-group input:-ms-input-placeholder {
    color: transparent;
  }
  
 #register-form .form-label-group input::-ms-input-placeholder {
    color: transparent;
  }
  
 #register-form .form-label-group input::-moz-placeholder {
    color: transparent;
  }
  
 #register-form .form-label-group input::placeholder {
    color: transparent;
  }
  
 #register-form .form-label-group input:not(:placeholder-shown) {
    padding-top: calc(var(--input-padding-y) + var(--input-padding-y) * (2 / 3));
    padding-bottom: calc(var(--input-padding-y) / 3);
  }
  
 #register-form .form-label-group input:not(:placeholder-shown)~label {
    padding-top: calc(var(--input-padding-y) / 3);
    padding-bottom: calc(var(--input-padding-y) / 3);
    font-size: 12px;
    color: #777;
  }
  
  .card-signin {
    border: 0;
    border-radius: 1rem;
    box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);
    overflow: hidden;
  }
  
  .card-signin .card-title {
    margin-bottom: 2rem;
    font-weight: 300;
    font-size: 1.5rem;
  }  
